@import '../../App.scss';

.navbar {
    @include themify($themes) {


display: flex;
align-items: center;
justify-content: space-between;
padding: 40px 20px;
height: 50px;
border-bottom: 1px solid lightgrey;
position: sticky;
top: 0;
background-color: themed('bg');
color: themed('textColor');
z-index: 88;


    .left{
        display: flex;
        align-items: center;
        gap: 30px;
        span{
            font-weight: bold;
            font-size: 20px;
            color: themed('logo');
        }

        .search{
            display: flex;
        align-items: center;
        gap: 10px;
        border: 1px solid themed('border');
        border-radius: 5px;
        padding: 5px;
            input{
                border: none;
                background-color: transparent;
                width: 500px;
                color: themed('textColor');
                outline: none;

                @include mobile {
                    display: none;
                }
                @include tablet {
                    width: 200px;
                }
            }
        }
    }

    .right{
        display: flex;
        align-items: center;
        gap: 20px;

        @include mobile {
            display: none;
        }
        @include tablet {
            display: none;
        }
        .user{
            display: flex;
            align-items: center;
            gap: 10px;
            font-weight: 500;

            img{
                width: 50px;
                height: 50px;border-radius: 50%;
                object-fit: cover;
            }

            span{

            }
        }
    }
}
}